import React, { useState } from "react";
import makeStyles from "@mui/styles/makeStyles";
import { Button, Typography } from "@mui/material";
import SideContent from "./SideContent";

const useStyles = makeStyles(() => ({
  container: {
    paddingTop: 16,
  },
  button: {
    justifyContent: "left !important",
    marginLeft: "32px !important",
    marginTop: "16px !important",
  },
  buttonText: {
    color: "#FFCB9A",
  },
  activeButtonText: {
    color: "#5af444",
  },
}));

const SideBar = () => {
  const classes = useStyles();
  const [content, setContent] = useState("about");

  return (
    <>
      <div className={classes.container}>
        <Button
          onClick={() => {
            setContent("about");
          }}
          disableRipple
          variant={content === "about" ? "contained" : "outlined"}
          className={classes.button}
        >
          {content === "about" ? (
            <Typography variant="button" className={classes.activeButtonText}>
              About
            </Typography>
          ) : (
            <Typography variant="button" className={classes.buttonText}>
              About
            </Typography>
          )}
        </Button>
        <Button
          onClick={() => {
            setContent("experience");
          }}
          disableRipple
          variant={content === "experience" ? "contained" : "outlined"}
          className={classes.button}
        >
          {content === "experience" ? (
            <Typography variant="button" className={classes.activeButtonText}>
              experience
            </Typography>
          ) : (
            <Typography variant="button" className={classes.buttonText}>
              experience
            </Typography>
          )}
        </Button>
        <Button
          onClick={() => {
            setContent("projects");
          }}
          disableRipple
          variant={content === "projects" ? "contained" : "outlined"}
          className={classes.button}
        >
          {content === "projects" ? (
            <Typography variant="button" className={classes.activeButtonText}>
              projects
            </Typography>
          ) : (
            <Typography variant="button" className={classes.buttonText}>
              projects
            </Typography>
          )}
        </Button>
      </div>
      <SideContent content={content} />
    </>
  );
};

export default SideBar;
